.App {
  font-family: "Open Sans", sans-serif;
}
body {
  background-color: orange;
  background: url("https://img2.pngio.com/ink-bottle-png-hd-png-pictures-vhvrs-spilled-ink-bottle-png-2338_1283.png");
  background-repeat: no-repeat;
}
.formDiv {
  position: inherit;
  width: auto;
  display: flex;
  justify-content: space-between;
  width: 100%;
  background-color: pink;
  height: auto;
}
.formsDivInside {
  margin-right: 100px;
  margin-top: 20px;
  position: inherit;
}
.blogsDiv1 {
  display: flex;
  justify-content: center;
}

.blogsDiv {
  background-color: pink;
  display: flex;
  justify-content: center;
  border-radius: 20px;
  min-width: 300px;
  max-width: 700px;
  text-align: center;
  margin-bottom: 30px;
  padding: 20px;
}
.p {
  min-width: 300px;
  max-width: 700px;
  word-wrap: break-word;
}
.author {
  padding-left: 500px;
  width: auto;
}
.blogsList {
  list-style-type: none;
}
.errorDiv,
.succesDiv {
  position: absolute;
  text-align: center;
  display: flex;
  justify-content: center;
  font-size: 30px;
  font-weight: 200;
  width: 100%;
}

.errorMessage {
  border: 1px solid red;
  padding-left: 20px;
  padding-right: 20px;
  border-radius: 13px;
  transition: 0.5s;
  color: red;
}
.succesMessage {
  border: 1px solid rgb(0, 255, 85);
  padding-left: 20px;
  padding-right: 20px;
  border-radius: 13px;
  transition: 0.5s;
  color: rgb(0, 255, 85);
}

.userNameDiv {
  position: inherit;
  justify-content: space-between;
  width: auto;
  display: flex;
  width: 100%;
  background-color: pink;
}

.nameUserName {
  display: block;
}
.username {
  opacity: 0.5;
}

.createBlogDiv {
  margin-top: 20px;
  position: relative;
  display: flex;
  justify-content: center;
}
.title {
  font-size: 30px;
}

button {
  border: none;
  margin: 5px;
  cursor: pointer;
}

.editForms {
  background-color: palegoldenrod;
  display: flexbox;
  position: absolute;
  margin-bottom: 500px;
  margin-left: 300px;
}

.deleteDiv {
  float: right;
}

.logo {
  display: flex;
  position: relative;
  width: 150px;
  height: 150px;
  margin-left: 100px;
  margin-bottom: 10px;
  background: url("https://images.squarespace-cdn.com/content/513a3459e4b08c563beff5d6/1366233640432-RWKKGDJJJPJ3K1ISYMJN/blog+header+icon.png?format=1000w&content-type=image%2Fpng");
  background-repeat: round;
}
